import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import ListItemText from "@mui/material/ListItemText";

const dispatchedEvents = [];

const SrsMuiListItemText = function (props, ref) {
  return (
    <ListItemText
      primary={props.primary}
      secondary={props.secondary}
      {...props}
      theme={props.muiTheme}
    >
      <slot />
    </ListItemText>
  );
};

const styles = `
  /* Custom styles for the MuiListItemText component */
`;

const props = [
  {
    name: "primary",
    defaultValue: "",
    type: "string",
  },
  {
    name: "secondary",
    defaultValue: "",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiListItemText),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
